    methods: {
        init () {
            this.$store.dispatch('getIndexData').then(res => {
                if (res.data.code === 1) {
                    let data = res.data.detail.htmlList || []
                    this.themeList.map(item => {
                        item.list = data.filter(dataItem => {
                            return dataItem.columnId === item.id + ''
                        }).map(filterItem => {
                            return {
                                href: filterItem.titleURL,
                                title: filterItem.title,
                                date: filterItem.addTime && filterItem.addTime.substring(0, 10).replace(/\//g, '-')
                            }
                        })
                    })
                    let newsData = data.filter(item => {
                        return this.news.id + '' === item.columnId
                    })
                    this.news.newsList = newsData.map(filterItem => {
                        return {
                            href: filterItem.titleURL,
                            title: filterItem.title
                        }
                    })
                    this.news.imageList = newsData.map(filterItem => {
                        return {
                            href: filterItem.titleURL,
                            title: filterItem.title,
                            imgSrc: filterItem.titleImg,
                            imgAlt: filterItem.title
                        }
