                        -webkit-transition: all .3s ease-out;
                        -o-transition: all .3s ease-out;
                        -ms-transition: all .3s ease-out;
                        transition: all .3s ease-out;
                        float: left;
                    }
                    li::before{
                        content: counter(li);
                        counter-increment: li;
                        position: absolute;
                        left: -1px;
                        top: 50%;
                        margin-top: -1.3em;
                        background: #409eff;
                        color: #fff;
                        height: 2em;
                        width: 2em;
                        line-height: 2em;
                        border: .3em solid #fff;
                        text-align: center;
                        font-weight: bold;
                        border-radius: 2em;
