                        display: block;
                        width: 80%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    time{
                        font-size: 12px;
                        color: #909399;
                        position: absolute;
                        top: 5px;
                        right: 10px;
                    }
                }
            }
