            float: left;
            position: relative;
            width: 400px;
            height: 225px;
            overflow: hidden;
            .banner{
                width: 1600px;
                position: absolute;
                left: 0;
                animation-name: bannerMove;
                animation-duration: 8s;
                animation-iteration-count: infinite;
                li{
                    position: relative;
                    float: left;
                    width: 400px;
                    height: 225px;
                }
            }
        }
